@use "../../scss/" as *;

.movie-card {
    position: relative;
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    // padding-top: 50%;
    border-radius: $border-radius;
    // margin-bottom: 1rem;
    overflow: hidden;

    .btn {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(2);
        opacity: 0;
        transition: transform 0.3s ease, box-shadow 0.3s ease, opacity 0.3s ease;
        z-index: 10;
    }

    img {
        transform: scale(1);
        transition: transform 0.3s ease;
    }

    h3 {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: $header-bg;
        opacity: 0.8;
        font-size: 0.8rem;
        padding: 0.5rem;
        color: $white;
        z-index: 11;
    }

    &::before {
        content: "";
        position: absolute;
        z-index: 5;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: $black;
        opacity: 0;
        transition: opacity 0.3s ease;
        border-radius: $border-radius;
    }

    @media (hover: hover) and (pointer: fine) {
        &:hover::before {
            opacity: 0.8;
        }

        &:hover .btn {
            transform: translate(-50%, -50%) scale(1);
            opacity: 1;
        }
        &:hover img {
            transform: scale(1.2);
        }
    }
}